.container {
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.container-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: calc(100vw - 224px);
}

.container-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;

  width: 978px;
  height: 592px;
}